import {reactive,toRefs,onMounted,defineComponent} from 'vue';
import {useRouter,useRoute} from 'vue-router';
import {getRequest,postRequest} from '../../request/api';
import url from '../../request/url';
import {token} from '../../libs/util';
import OrderList from '../.././components/OrderList';
import OrderItem from '../../components/OrderItem';
import { Toast,Dialog } from 'vant';
export default defineComponent({
    name:'pay',
    components:{
        OrderList,
        OrderItem
    },
    setup () {
        let state = reactive({
            chosenAddressId:'',
            currentAddress:{},
            list:[],
            productInfo: {
                count: 0,
                total: 0,
            },
            products: [],
            isOpen: false,
        })

        let {back,getShopbagBySids,getAddressData,showSelectAddressList,payBox,newAddress,editAddress,selectAddress} = methods(state);

        onMounted(() => {
            getShopbagBySids();
            getAddressData();
        })

        return {
            ...toRefs(state),
            back,
            showSelectAddressList,
            payBox,
            newAddress,
            editAddress,
            selectAddress
        }
    }
})

function methods (state) {
    let tokenString = token('token');
    let router = useRouter();
    let {sids} = useRoute().query;

    let back = () => {
        router.back();
    }

    //根据sids获取购物袋数据
    let getShopbagBySids = () => {
        let data = Object.create(null);
        Reflect.set(data,'sids',JSON.stringify(sids.split('-')));
        getRequest(url.commitShopcart,data).then(({code,result,msg}) => {
            if(code == 50000){
                if(!Reflect.get(result,'length')) return router.push({ name: "home" });
                result.map(item => {
                    state.productInfo.count += item.count;
                    state.productInfo.total += item.count * item.price;
                });
                Reflect.set(state,'products',result);
            }else{
                Toast(msg);
            }
        }).catch((err) => {
            console.log("err=>",err);
        });
    }
    //地址
    let getAddressData = () => {
        getRequest(url.findAddress).then(({code,result}) => {
            if(code == 20000){
                result.map(item => {
                    Reflect.set(item,'isDefault',Boolean(item.isDefault));
                    Reflect.set(item,'id',item.aid);
                    Reflect.set(item,'address',`${item.province}${item.city}${item.county}${item.addressDetail}`);
                    if(Reflect.get(item,'isDefault')){
                        Reflect.set(state,'chosenAddressId',item.aid);
                        Reflect.set(state,'currentAddress',item);
                    }
                });
                Reflect.set(state,'list',result);
            }
        }).catch((err) => {
            console.log("err==>",err);
        });
    }

    let showSelectAddressList = () => {
        Reflect.set(state,'isOpen',true);
    }
    let pay = () => {
        let data = Object.create(null);
        Reflect.set(data,'sids',JSON.stringify(sids.split('-')));
        Reflect.set(data,'phone',state.currentAddress.tel);
        Reflect.set(data,'address',state.currentAddress.address);
        Reflect.set(data,'receiver',state.currentAddress.name);
        postRequest(url.pay,data).then(({code,msg}) => {
            code == 60000 && router.push({ name: "order" });
            Toast(msg);
        }).catch((err) => {
            console.log("err==>",err);
        });
    }
    //结算
    let payBox = () => {
        Dialog.confirm({
            title: '确认订单',
            message:'是否立即结算?',
            confirmButtonColor:'#0C34BA'
        }).then(() => {
            pay();
        }).catch(() => {
            Toast('取消结算');
        });
    }

    let newAddress = () => {
        router.push({ name: "newAddress" });
    }

    let editAddress = ({aid}) => {
        router.push({ name: "newAddress", query: { aid } });
    }

    let selectAddress = (item) => {
        Reflect.set(state,'isOpen',false);
        Reflect.set(state,'currentAddress',item);
    }

    return {
        back,
        getShopbagBySids,
        getAddressData,
        showSelectAddressList,
        payBox,
        newAddress,
        editAddress,
        selectAddress
    }
}