<template>
    <div class="pay">
        <van-nav-bar title="订单结算" left-text="返回" left-arrow @click-left="back" />
        <div class="pay-box">
            <div class="select-address">
                <div>
                    <span @click="showSelectAddressList">选择地址</span>
                    <van-icon name="arrow" />
                </div>
                <div class="user-box">
                    <div class="user-info">
                        <div class="user-name">{{ currentAddress.name }}</div>
                        <div class="user-phone">{{ currentAddress.tel }}</div>
                        <div class="default-address" v-if="currentAddress.isDefault">
                            <span>默认</span>
                        </div>
                    </div>
                    <div class="detail-address">{{ currentAddress.address }}</div>
                </div>
            </div>
            <!-- 订单信息 -->
            <div class="order-info">
                <order-list
                    info-title-left="订单信息"
                    :count="productInfo.count"
                    :total="productInfo.total"
                >
                    <order-item v-for="(item, index) in products" :item="item" :key="index"></order-item>
                </order-list>
            </div>
        </div>

        <div class="btn-box">
            <van-button type="primary" color="#0C34BA" block round @click="payBox">立即结算</van-button>
        </div>

        <van-popup v-model:show="isOpen" position="bottom" closeable >
            <div class="address-title"><span>选择地址</span></div>
            <van-address-list
                v-model="chosenAddressId"
                :list="list"
                default-tag-text="默认"
                @add="newAddress"
                @edit="editAddress"
                @select="selectAddress"
            />
        </van-popup>
    </div>
</template>

<script>
    export { default } from './pay';
</script>

<style lang="less" scoped>
    @import './pay.less';
</style>